<template lang="pug">
  v-container.my-entrance(fluid fill-height)
    v-toolbar(
      color="primary"
      flat
      dark
      fixed
      app
      dense
    )
      v-btn(icon)
        img.my-entrance__logo(src='~/@/assets/images/logo.svg' alt='VueAMazeInn Logo')
      v-spacer
      v-btn(flat)
        | GitHub
      v-btn(flat)
        | Tutorial
    v-layout(justify-center align-center)
      v-flex.text-xs-center(xs12 lg6)
        v-layout(row wrap)
          v-flex(xs12)
            .my-entrance__logo-name
              img(src='~/@/assets/images/logo-name.svg' alt='VueExample')
          v-flex(xs12)
            .my-entrance__subheading.subheading
              | Welcome to AMazeInn!
          v-flex(xs12)
            v-card.my-entrance__card
              v-card-title.my-entrance__card-title(primary-title)
                v-avatar(:size="100")
                  img(src='avatar' alt='Avatar')

              v-card-text
                v-form
                  v-text-field(
                    label='Name'
                    :hint='nameHint'
                    v-model='credentials.username'
                    dark
                    required
                  ) p(v-model='')
                  v-text-field(
                    label='Password'
                    :hint='passwordHint'
                    v-model='credentials.password'
                    min='8'
                    :append-icon="passwordHidden ? 'visibility' : 'visibility_off'"
                    @click:append='() => (passwordHidden = !passwordHidden)'
                    :type="passwordHidden ? 'password' : 'text'"
                    counter=''
                    dark
                  )

              v-card-actions.my-entrance__card-actions
                v-btn(
                  :loading="checkinLoading"
                  @click="guestCheckin()"
                  block
                  color='accent'
                  dark
                ) Check-in
              v-card-actions.my-entrance__card-actions
                v-btn(
                  :loading="enterLoading"
                  @click="guestEnter()"
                  block
                  color='accent'
                  dark
                ) Login
</template>

<script>
import api from '@/auth/helpers'
import auth from '@/auth/index'

export default {
  name: 'Entrance',

  data () {
    return {
      credentials: {
        username: 'mocker',
        password: '111111111'
      },
      avatar: this.$store.state.auth.user.avatar,
      nameHint: '',
      passwordHint: '',
      nameInvalid: false,
      passwordInvalid: false,
      passwordHidden: true,
      checkinLoading: false,
      enterLoading: false
    }
  },

  watch: {
    'credentials.username': 'nameCheck',
    'credentials.password': 'passwordCheck'
  },

  methods: {

    nameCheck (val, oldVal) {
      var label = api.checkName(val)
      if (label) {
        this.nameInvalid = true
        this.nameHint = label
      } else {
        this.nameInvalid = false
        this.nameHint = ''
      }
    },

    passwordCheck (val, oldVal) {
      var label = api.checkPassword(val)
      if (label) {
        this.passwordInvalid = true
        this.passwordHint = label
      } else {
        this.passwordInvalid = false
        this.passwordHint = ''
      }
    },

    oldPath () {
      var url = new URL(document.location.href)
      var params = new URLSearchParams(url.search)
      var redirect = params.get('redirect')
      if (redirect) {
        redirect = redirect.slice(1)
      }
      return redirect
    },

    guestCheckin () {
      const credentials = {
        username: this.credentials.username,
        password: this.credentials.password
      }
      if (!this.nameInvalid && !this.passwordInvalid) {
        this.checkinLoading = true
        api.publicRequest(api.checkinConfig(JSON.stringify(credentials)))
          .then(res => {
            this.checkinLoading = false
            console.log('Checkin OK')
          })
          .catch((error) => {
            this.checkinLoading = false && error
          })
      } else {
        api.showMessage('Please check your input.')
      }
    },

    guestEnter () {
      const credentials = {
        username: this.credentials.username,
        password: this.credentials.password
      }
      if (!this.nameInvalid && !this.passwordInvalid) {
        this.enterLoading = true
        var redirect = this.oldPath()
        api.publicRequest(api.enterConfig(JSON.stringify(credentials)))
          .then(res => {
            auth.storeToken(res)
            if (redirect) {
              auth.redirectPath(redirect)
            } else {
              auth.redirectPath('home')
            }
            console.log('Enter OK')
          })
          .catch((error) => {
            this.enterLoading = false && error
          })
      } else {
        api.showMessage('Please check your input.')
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .my-entrance
    background-image: $app-login-image
    background-size: 100% 100%

    &__logo
      max-width: 46px
      padding-left: 12px
      padding-top: 4px

    &__logo-name

      img
        max-width: 200px
        width: 100%

    &__subheading
      color: white
      padding-top: 10px
      padding-bottom: 20px

    &__card
      background-color: $app-card
      max-width: 370px
      margin: 0 auto

    &__card-title
      justify-content: center

    &__card-actions
      justify-content: center

</style>



